.caixa-texto {
    font-size: 1rem;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: block;
    width: 100%;
    padding: 0.8rem;
    margin: 0 0 1rem;
    border: 1px solid rgba(51, 30, 54, 1);
    border-radius: 5px;
}

.caixa-texto:focus {
    outline: none;
    border-color: rgba(110, 164, 191, 1);
    -webkit-box-shadow: 0 0 10px rgba(110, 164, 191, 1);
            box-shadow: 0 0 10px rgba(110, 164, 191, 1);
}


