.navbar {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex: 0 1 60px;
        flex: 0 1 60px;
    min-height: 60px;
    padding: 0 1rem;
    background: #ffffff;
    border-bottom: 1px solid #d4dadf;
    -webkit-box-shadow: 0 1px 1px 0 #f0f1f4;
            box-shadow: 0 1px 1px 0 #f0f1f4;
}

.navbar__logo {
    display: block;
    width: 200px;
}

@media (min-width: 992px) {
    .navbar {
        padding: 0 10%;
    }
}


